import request from '@/api/request.js';

// 
export const submitHotel = (data) => {
  return request({
    url: '/ims-webapp/hotel/item',
    method: 'POST',
    data: data,
  });
};
// 
export const updateHotel = (data) => {
  return request({
    url: '/ims-webapp/hotel/item',
    method: 'PUT',
    data: data,
  });
};

